@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;

500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
  list-style: none;
  outline: none;
}

::selection {
  background-color: rgb(224, 27, 55);
  color: rgb(255, 255, 255);
}

html {
  scroll-behavior: smooth;
}

body {
  background: linear-gradient(rgb(255, 255, 255), rgb(249, 249, 251), rgb(255, 255, 255));
}

body.active-dark {
  overflow-y: scroll;
  overflow: hidden;
}

body.active-dark::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.788);
  z-index: 500;
}

body.active-dark .header__section.nav-change {
  background-color: rgba(0, 0, 0, 0);
}

body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-thumb {
  background: linear-gradient(rgb(85, 85, 85), rgb(32, 32, 32), rgba(0, 0, 0, 0.541));
}

body::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(rgb(190, 190, 190), rgb(0, 0, 0), rgb(54, 54, 54));
}

.container {
  max-width: 1520px;
  margin: 0 auto;
  padding: 0 20px;
}

.title_item {
  display: flex;
  align-items: center;
  column-gap: 14px;
}

.title_item .title {
  font-size: 32px;
  font-weight: 600;
  letter-spacing: 1px;
}

header {
  background-image: url("../images/header__district.jpg");
  background-position: top left;
  background-repeat: no-repeat;
  background-size: contain;
  height: 55em;
}

.header__section {
  position: fixed;
  top: 0;
  left: 50%;
  width: 100%;
  height: 120px;
  transform: translateX(-50%);
  z-index: 700;
  transition: 0.2s ease;
}

.header__section.nav-change {
  background-color: rgba(245, 245, 245, 0.9);
  height: 90px;
}

.header__section.nav-change:hover {
  background-color: rgb(245, 245, 245);
}

.header__section.nav-change .header__navbar {
  height: 90px;
}

.header__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 120px;
  transition: 0.2s ease;
}

.header__navbar .logo {
  max-width: 144px;
  min-width: 100px;
}

.header__navbar .logo img {
  width: 100%;
}

.header__navbar .nav-links ul {
  display: flex;
  align-items: center;
}

.header__navbar .nav-links ul li a {
  position: relative;
  padding: 2px 25px;
  font-size: 18px;
  font-weight: 500;
  color: rgb(0, 0, 0);
  transition: 0.2s ease;
}

.header__navbar .nav-links ul li a::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background-color: rgb(224, 27, 55);
  transition: 0.2s ease;
}

.header__navbar .nav-links ul li a:hover {
  color: rgb(224, 27, 55);
}

.header__navbar .nav-links ul li a:hover::after {
  width: 23px;
}

.header__navbar .nav-links ul li a.active-link {
  color: rgb(224, 27, 55);
}

.header__navbar .nav-links ul li a.active-link::after {
  width: 23px;
}

.header__navbar .nav-links ul .nav__btn {
  padding: 10px 22px;
  border: none;
  border: 2px solid rgb(224, 27, 55);
  border-radius: 10px;
  font-weight: 500;
  font-size: 18px;
  background-color: rgb(224, 27, 55);
  color: rgb(255, 255, 255);
  margin-left: 25px;
  cursor: pointer;
  transition: 0.2s ease;
}

.header__navbar .nav-links ul .nav__btn:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(224, 27, 55);
}

.header__navbar .navside {
  position: fixed;
  top: 0;
  right: 0;
  width: 350px;
  max-width: 100%;
  height: 100vh;
  padding: 90px 20px;
  transform: translateX(110%);
  background-color: rgba(20, 20, 20, 0.973);
  transition: 0.3s ease;
}

.header__navbar .navside.show-nav {
  transform: translateX(0);
}

.header__navbar .navside .close__btn .bar {
  position: absolute;
  top: 50%;
  left: 20%;
  width: 28px;
  height: 3px;
  background-color: rgb(255, 255, 255);
  transform: translate(-50%, -50%);
}

.header__navbar .navside .close__btn {
  position: absolute;
  top: 35px;
  right: 15px;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.header__navbar .navside .close__btn .bar1 {
  transform: rotate(-45deg);
}

.header__navbar .navside .close__btn .bar2 {
  transform: rotate(45deg);
}

.header__navbar .navside ul li a {
  position: relative;
  font-size: 18px;
  font-weight: 500;
  display: block;
  color: rgb(255, 255, 255);
  padding: 12px 0;
  transition: 0.2s ease;
}

.header__navbar .navside ul li a::after {
  content: "";
  position: absolute;
  top: 90%;
  left: 8%;
  width: 0;
  height: 2px;
  transform: translate(-50%, -50%);
  background-color: rgb(224, 27, 55);
  transition: 0.2s ease;
}

.header__navbar .navside ul li a:hover {
  color: rgb(224, 27, 55);
}

.header__navbar .navside ul li a:hover::after {
  width: 23px;
}

.header__navbar .navside ul .nav__btn {
  margin-top: 25px;
  padding: 10px 22px;
  border: none;
  border: 2px solid rgb(224, 27, 55);
  border-radius: 10px;
  font-weight: 500;
  font-size: 18px;
  background-color: rgb(224, 27, 55);
  color: rgb(255, 255, 255);
  cursor: pointer;
  transition: 0.2s ease;
}

.header__navbar .navside ul .nav__btn:hover {
  background: none;
  border: 2px solid rgb(255, 255, 255);
}

.header__navbar .burger .bar {
  width: 28px;
  height: 3px;
  background-color: rgb(0, 0, 0);
  margin: 6px 0;
}

.header__navbar .burger {
  cursor: pointer;
  display: none;
}

.header__content {
  padding-top: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 20px;
}

.header__text_item {
  max-width: 630px;
}

.header__text_item .header__title_item {
  display: flex;
  align-items: center;
}

.header__text_item .header__title_item .title_border_item {
  max-width: 20px;
}

.header__text_item .header__title_item .title_border_item .title_border {
  width: 100%;
}

.header__text_item .header__title {
  font-size: 48px;
  font-weight: 600;
  margin-left: 18px;
  line-height: 72px;
}

.header__text_item .header__title span {
  font-weight: 700;
  color: rgb(224, 27, 55);
}

.header__text_item .header__text {
  max-width: 588px;
  margin-top: 18px;
  font-size: 20px;
  font-weight: 400;
  line-height: 36px;
  letter-spacing: 1px;
}

.header__text_item .header__buttons {
  display: flex;
  column-gap: 21px;
  margin-top: 36px;
}

.header__text_item .header__buttons .header__btn {
  padding: 12px 24px;
  border: none;
  border: 2px solid rgb(224, 27, 55);
  border-radius: 10px;
  background-color: rgb(224, 27, 55);
  color: rgb(255, 255, 255);
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  transition: 0.2s ease;
}

.header__text_item .header__buttons .header__btn:hover {
  background: none;
  color: rgb(224, 27, 55);
}

.header__text_item .header__buttons .header__btn:nth-child(2) {
  background: none;
  color: rgb(224, 27, 55);
}

.header__text_item .header__buttons .header__btn:nth-child(2) .btn_play {
  margin-right: 8px;
}

.header__text_item .header__buttons .header__btn:nth-child(2):hover {
  background-color: rgb(224, 27, 55);
  color: rgb(255, 255, 255);
}

.header__image_item {
  max-width: 630px;
}

.header__image_item img {
  width: 100%;
}

.toUpBtn {
  position: fixed;
  top: 130%;
  right: 2%;
  z-index: 500;
  transition: 0.2s ease;
}

.toUpBtn a {
  font-size: 35px;
  background-color: rgba(224, 27, 55, 0.9);
  border: 1px solid rgb(224, 27, 55);
  color: rgb(255, 255, 255);
  padding: 5px 15px;
  transition: 0.2s ease;
}

.toUpBtn a:hover {
  background: none;
  color: rgb(224, 27, 55);
}

.toUpBtn.show-btn {
  position: fixed;
  top: 88%;
}

.choose__section {
  margin-top: 180px;
}

.choose__items {
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
  margin-top: 36px;
}

.choose__items .choose__image_item {
  max-width: 760px;
}

.choose__items .choose__image_item img {
  width: 100%;
}

.choose__items .choose__selects__item {
  width: 760px;
  overflow: hidden;
}

.choose__items .choose__selects__item .select__item {
  position: relative;
  margin-top: 16px;
  background: rgb(255, 255, 255);
  border-radius: 20px;
  width: 100%;
  padding: 18px;
  height: 75px;
  cursor: pointer;
  transition: 0.1s ease;
}

.choose__items .choose__selects__item .select__item .title__items {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.choose__items .choose__selects__item .select__item .select__item_title {
  display: flex;
  align-items: center;
}

.choose__items .choose__selects__item .select__item .select__item_title .item {
  width: 16px;
  height: 16px;
  background-color: rgb(224, 27, 55);
  border-radius: 50%;
  margin-right: 16px;
}

.choose__items .choose__selects__item .select__item .select__item_title .item__title {
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 1px;
}

.choose__items .choose__selects__item .select__item .select__item_title .item__title::selection {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
}

.choose__items .choose__selects__item .select__item .select__icon .arrow_down {
  font-size: 27px;
  transition: 0.2s ease;
}

.choose__items .choose__selects__item .select__item .select__text_item {
  margin-top: 18px;
  opacity: 0;
  transition: 0.2s ease;
}

.choose__items .choose__selects__item .select__item .select__text_item .text_item {
  font-size: 20px;
  font-weight: 400;
  line-height: 36px;
  color: rgba(0, 0, 0, 0.6);
}

.choose__items .choose__selects__item .select__item.show-text {
  height: 210px;
}

.choose__items .choose__selects__item .select__item.show-text .select__text_item {
  opacity: 1;
}

.choose__items .choose__selects__item .select__item.show-text .select__text_item .text_item {
  cursor: auto;
}

.choose__items .choose__selects__item .select__item.show-text .select__icon .arrow_down {
  transform: rotate(180deg);
}

.services__section {
  padding-top: 116px;
}

.service__items {
  margin-top: 36px;
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
}

.service__items .service__item {
  background-color: rgb(255, 255, 255);
  max-width: 380px;
  padding: 18px;
  border-radius: 20px;
}

.service__items .service__item .service__image_item {
  max-width: 270px;
  margin: 0 auto;
}

.service__items .service__item .service__image_item img {
  width: 100%;
}

.service__items .service__item .service__item_title {
  font-size: 24px;
  font-weight: 600;
  color: rgb(224, 27, 55);
  margin-top: 30px;
}

.service__items .service__item .service__item_text {
  margin-top: 8px;
  font-size: 15px;
  line-height: 28px;
  letter-spacing: 1px;
}

.portfolio__section {
  padding-top: 116px;
}

.portfolio__items {
  margin-top: 36px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
}

.portfolio__items .portfolio__item {
  overflow: hidden;
}

.portfolio__items .portfolio__item .portfolio__image {
  width: 100%;
  height: 300px;
  background-color: rgb(0, 0, 0);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.portfolio__items .portfolio__item .portfolio__image img {
  max-width: 280px;
  width: 100%;
  transition: 0.2s ease;
}

.portfolio__items .portfolio__item:hover .portfolio__image img {
  transform: scale(1.1);
}

.team__section {
  padding-top: 116px;
}

.team__items {
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
}

.team__items .team__item {
  max-width: 370px;
  margin-top: 36px;
}

.team__items .team__item img {
  width: 100%;
}

.team__items .team__item .person__profession {
  margin-top: 18px;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 1px;
  color: rgb(224, 27, 55);
}

.team__items .team__item .person__name {
  margin-top: 2px;
  font-size: 24px;
  font-weight: 600;
  letter-spacing: 1px;
}

.about__section {
  padding-top: 116px;
}

.about__items {
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
}

.about__items .about__image_item {
  max-width: 760px;
}

.about__items .about__image_item img {
  width: 100%;
}

.about__items .about__text_item {
  max-width: 500px;
  margin: 0 auto;
}

.about__items .about__text_item .about__text {
  margin-top: 24px;
  font-size: 18px;
  line-height: 32px;
  letter-spacing: 1px;
}

.footer__section {
  margin-top: 132px;
  padding: 55px 0;
  border-top: 1px solid rgba(0, 0, 0, 0.3);
}

.footer__items {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__items .footer__logo_item {
  max-width: 144px;
}

.footer__items .footer__logo_item img {
  width: 100%;
}

.footer__items .footer__social_links_item a {
  margin: 0 10px;
  font-size: 22px;
  color: rgb(0, 0, 0);
  transition: 0.2s ease;
  padding: 10px;
}

.footer__items .footer__social_links_item a:hover {
  color: rgb(224, 27, 55);
}

.footer__items .telephone p {
  font-size: 18px;
  letter-spacing: 1px;
}

/* ============================= media queries for responsive ================= */

@media screen and (min-width: 993px) {
  .header__navbar .navside {
    position: absolute;
    right: -110%;
  }
}

@media screen and (max-width: 1680px) {
  .header__content {
    padding-top: 150px;
  }
}

@media screen and (max-width: 1200px) {
  .service__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 20px;
  }

  .service__items .service__item {
    max-width: 100%;
  }

  .portfolio__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 1070px) {
  .team__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 20px;
  }

  .team__items .team__item {
    max-width: 100%;
  }

  .about__items {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
  }

  .about__items .about__text_item {
    margin: 0;
  }
}

@media screen and (max-width: 992px) {
  .header__navbar .nav-links {
    display: none;
  }

  .header__navbar .burger {
    display: block;
  }

  .header__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 20px;
  }

  .choose__section {
    margin-top: 280px;
  }

  .choose__items {
    display: flex;
    flex-direction: column;
  }

  .choose__items .choose__image_item {
    max-width: 100%;
  }

  .choose__items .choose__selects__item {
    width: 100%;
  }
}

@media screen and (max-width: 820px) {
  .portfolio__items .portfolio__item .portfolio__image {
    height: 200px;
  }
}

@media screen and (max-width: 768px) {
  .portfolio__items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  .portfolio__items .portfolio__item .portfolio__image {
    height: 300px;
  }
}

@media screen and (max-width: 600px) {
  .header__text_item .header__title {
    font-size: 38px;
    line-height: 62px;
  }

  .header__text_item .header__title_item .title_border_item {
    max-width: 15px;
  }

  .service__items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  .team__items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  .footer__items {
    display: flex;
    flex-direction: column;
    row-gap: 40px;
  }
}

@media screen and (max-width: 490px) {
  .header__text_item .header__title {
    font-size: 28px;
    line-height: 42px;
  }

  .header__text_item .header__title_item .title_border_item {
    max-width: 10px;
  }

  .header__text_item .header__text {
    font-size: 20px;
    line-height: 30px;
  }

  .choose__items .choose__selects__item {
    overflow-y: scroll;
  }

  .choose__items .choose__selects__item::-webkit-scrollbar {
    width: 0;
  }

  .choose__items .choose__selects__item .select__item.show-text {
    height: 200px;
  }

  .choose__items .choose__selects__item .select__item .select__text_item .text_item {
    font-size: 18px;
    line-height: 29px;
  }

  .portfolio__items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  .portfolio__items .portfolio__item .portfolio__image {
    height: 250px;
  }

  .about__items .about__text_item .about__text {
    font-size: 16px;
    line-height: 28px;
  }
}

@media screen and (max-width: 428px) {
  .title_item .title {
    font-size: 25px;
  }

  .header__text_item .header__text {
    font-size: 17px;
  }

  .header__text_item .header__buttons {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
  }

  .choose__section {
    margin-top: 116px;
  }
}

@media screen and (max-width: 370px) {
  .header__text_item .header__title {
    font-size: 25px;
    line-height: 38px;
  }
}